<template>
	<v-dialog v-model="proxyIsOpened" max-width="1216px">
		<v-card class="pa-10 pb-0">
			<v-flex class="d-flex justify-end">
				<v-btn
					icon
					@click="closeThis"
				>
					<v-icon>mdi-close</v-icon>
				</v-btn>
			</v-flex>
			<v-card-text class="pa-0">
				<v-container>
					<v-row>
						<v-col>
							<v-select
								:items="complectations"
								label="Комплектация"
								item-text="name"
								item-value="name"
								outlined
							></v-select>
						</v-col>
						<v-col>
							<v-select
								:items="colors"
								label="Цвет"
								item-text="name"
								item-value="name"
								outlined
								@input="addColor"
							>
							<template v-slot:item="{ item }">
								<div class="d-flex">
									<v-sheet width="22" height="22" :style="{'background-color': item.value }" class="mr-2"></v-sheet>{{ item.name }}
								</div>
							</template>
							</v-select>
						</v-col>
					</v-row>
					<v-row>
						<v-col>
							<v-chip v-for="color in selectedColors"
								class="ma-2"
								close
								:color="color.value"
								:key="color.name"
								@click:close="removeColor(color.name)"
								>
								{{ color.name }}
							</v-chip>
						</v-col>
					</v-row>
				</v-container>
			</v-card-text>

			<v-card-actions class="pa-0">
				<v-container>
					<v-row>
						<v-col>
							<v-divider></v-divider>
						</v-col>
					</v-row>
					<v-row>
						<v-col>
							<v-btn color="success" dark @click="closeThis">Сохранить</v-btn>
						</v-col>
					</v-row>
				</v-container>
			</v-card-actions>
		</v-card>
	</v-dialog>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
	name: 'CarPageAttachColorPopup',
	props: {
		isOpened: {
			type: Boolean,
			requared: true
		}
	},
	data () {
		return {
			colors: [
				{
					name: 'Red',
					value: '#ff0000'
				},
				{
					name: 'Green',
					value: '#00ff00'
				},
				{
					name: 'Blue',
					value: '#0000ff'
				},
			],
			selectedColors: [],
		}
	},
	computed: {
		...mapGetters({
			complectations: 'GET_COMPLECTATIONS',
		}),
		proxyIsOpened: {
			get () {
				return this.isOpened
			},
			set () {
				this.$emit('close-dialog')
			}
		},
	},
	methods: {
		closeThis () {
			this.$emit('close-dialog')
		},
		addColor (value) {
			if (this.selectedColors.some(color=>color.name === value)) {
				this.$root.$emit('alert',{text:'Цвет уже есть в списке', type:'error'})
			} else {
				this.selectedColors.push(this.colors[this.colorPos(this.colors, value)])
			}
		},
		removeColor (value) {
			this.selectedColors.splice(this.colorPos(this.selectedColors, value), 1);
		},
		colorPos (colors, value) {
			let pos;

			colors.forEach((color, i) => {
				if (color.name === value) {
					pos = i
				}
			});

			return pos
		}
	}
}
</script>

