<template>
	<v-dialog v-model="proxyIsOpened" max-width="1216px">
		<v-card class="pa-10 pb-0">
			<v-flex class="d-flex justify-end">
				<v-btn
					icon
					@click="closeThis"
				>
					<v-icon>mdi-close</v-icon>
				</v-btn>
			</v-flex>
			<v-card-text class="pa-0">
				<v-container>
					<v-row>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.name" label="Name"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.shortDescription" label="Short Description"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.carType" label="Car Type"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.order" label="Порядок"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.price" label="Цена" v-money="money"></v-text-field>
						</v-col>
					</v-row>
					<v-row>
						<v-col>
							<v-divider></v-divider>
						</v-col>
					</v-row>
					<v-row>
						<v-col cols="12" sm="6" md="5">
							<v-card outlined min-height="400">
								<v-card-text>
									<v-select
										v-model="currentItem"
										:items="items"
										item-text="name"
										item-value="name"
									></v-select>
									<v-list-item v-for="item in listItems" :key="item">
										<v-list-item-content>
											{{ item }}
										</v-list-item-content>
										<v-list-item-icon>
											<v-tooltip top>
												<template v-slot:activator="{ on }">
													<v-icon
														small
														@click="addValue(item)"
														v-on="on"
													>
														add
													</v-icon>
												</template>
												<span>Добавить</span>
											</v-tooltip>
										</v-list-item-icon>
									</v-list-item>
								</v-card-text>
							</v-card>
						</v-col>
						<v-col cols="12" md="2"></v-col>
						<v-col cols="12" sm="6" md="5">
							<v-card outlined min-height="400">
								<v-card-text>
									<v-card-subtitle>Данные</v-card-subtitle>
									<v-list-item v-for="item in list2" :key="item">
										<v-list-item-content>
											{{ item }}
										</v-list-item-content>
										<v-list-item-icon>
											<v-tooltip top>
												<template v-slot:activator="{ on }">
													<v-icon
														small
														@click="removeValue(item)"
														v-on="on"
														color="error"
													>
														remove
													</v-icon>
												</template>
												<span>Удалить</span>
											</v-tooltip>
										</v-list-item-icon>
									</v-list-item>
								</v-card-text>
							</v-card>
						</v-col>
					</v-row>
					<v-row>
						<v-col cols="12" sm="6" md="5">
							<v-card outlined min-height="400">
								<v-card-text>
									<v-card-subtitle>Пакеты</v-card-subtitle>
									<v-list-item v-for="item in packets" :key="item">
										<v-list-item-content>
											{{ item }}
										</v-list-item-content>
										<v-list-item-icon>
											<v-tooltip top>
												<template v-slot:activator="{ on }">
													<v-icon
														small
														@click="addPacket(item)"
														v-on="on"
													>
														add
													</v-icon>
												</template>
												<span>Добавить</span>
											</v-tooltip>
										</v-list-item-icon>
									</v-list-item>
								</v-card-text>
							</v-card>
						</v-col>
						<v-col cols="12" md="2"></v-col>
						<v-col cols="12" sm="6" md="5">
							<v-card outlined min-height="400">
								<v-card-text>
									<v-card-subtitle>Данные</v-card-subtitle>
									<v-list-item v-for="item in list3" :key="item">
										<v-list-item-content>
											{{ item }}
										</v-list-item-content>
										<v-list-item-icon>
											<v-tooltip top>
												<template v-slot:activator="{ on }">
													<v-icon
														small
														@click="removePacket(item)"
														v-on="on"
														color="error"
													>
														remove
													</v-icon>
												</template>
												<span>Удалить</span>
											</v-tooltip>
										</v-list-item-icon>
									</v-list-item>
								</v-card-text>
							</v-card>
						</v-col>
					</v-row>
				</v-container>
			</v-card-text>

			<v-card-actions class="pa-0">
				<v-container>
					<v-row>
						<v-col>
							<v-divider></v-divider>
						</v-col>
					</v-row>
					<v-row>
						<v-col>
							<v-btn color="success" dark @click="closeThis">Сохранить</v-btn>
						</v-col>
					</v-row>
				</v-container>
			</v-card-actions>
		</v-card>
	</v-dialog>
</template>

<script>
import { mapGetters } from 'vuex'
import {VMoney} from 'v-money'

export default {
	name: 'CarPageCreateComplectationPopup',
	props: {
		isOpened: {
			type: Boolean,
			requared: true
		},
		id: {
			type: Number,
			requared: true
		}
	},
	directives: {money: VMoney},
	data () {
		return {
			editedItem: {
				name: '',
				shortDescription: '',
				carType: '',
				order: 0,
				price: 0
			},
			currentItem: 'Двигатель',
			items: [
				{
					name: 'Двигатель',
					props: [
						'Объём',
						'Автоматическая КП',
						'Мощность'
					]
				},
				{
					name: 'Подвеска',
					props: [
						'Колёсная база',
						'Мнимальный дорожный просвет',
						'Передняя колея'
					]
				}
			],
			packets: ['Winter', 'Winner', 'Sport'],
			list2: [],
			list3: [],
			money: {
				thousands: ' ',
				precision: 0
			}
		}
	},
	computed: {
		...mapGetters({
			complectations: 'GET_COMPLECTATIONS',
		}),
		proxyIsOpened: {
			get () {
				return this.isOpened
			},
			set () {
				this.$emit('close-dialog')
			}
		},
		listItems () {
			let arr = [];

			this.items.forEach(item => {
				if (item.name === this.currentItem) arr = item.props
			});

			return arr;
		}
	},
	methods: {
		closeThis () {
			this.$emit('close-dialog')
		},
		addValue (item) {
			if (this.list2.indexOf(item) < 0) {
				this.list2.push(item)
			} else {
				this.$root.$emit('alert', {text: 'Такой элемент уже есть в списке', type: 'error'})
			}
		},
		removeValue (item) {
			this.list2.splice(this.list2.indexOf(item),1)
		},
		addPacket (item) {
			if (this.list3.indexOf(item) < 0) {
				this.list3.push(item)
			} else {
				this.$root.$emit('alert', {text: 'Такой пакет уже есть в списке', type: 'error'})
			}
		},
		removePacket (item) {
			this.list3.splice(this.list3.indexOf(item),1)
		}
	},
	watch: {
		id () {
			if (this.id !== 0) {
				this.complectations.forEach(mod => {
					if (mod.id === this.id) {
						Object.assign(this.editedItem, mod)
					}
				});
			} else {
				Object.assign(this.editedItem,  {
					name: '',
					shortDescription: '',
					carType: '',
					order: 0,
					price: 0
				})
			}
		}
	}
}
</script>

