<template>
	<v-container class="pt-0 pb-0">
		<v-row v-show="!showElements && label !== 'Цвет'">
			<v-col>
				<car-page-tabs :label="label" :title="title" :buttonText="buttonText" :items="items" :current-item="currentItem" @show-elements="showElements = true"></car-page-tabs>
			</v-col>
		</v-row>

		<v-row v-show="showElements">
			<v-col>
				<car-page-line :label="label" show-drop-down :items="items" :current-item="currentItem"></car-page-line>
			</v-col>
		</v-row>

		<v-row v-if="label === 'Цвет'">
			<v-col>
				<v-toolbar flat color="white" height="100" extended>
					<v-toolbar-title class="font-weight-bold">
						{{ title }}
					</v-toolbar-title>
					
					<v-btn color="success" dark class="ml-auto" @click="openAttachColor">{{ buttonText }}</v-btn>
					<template #extension>
						<v-toolbar-items>
							<v-switch v-model="interior" label="Интерьер" class="mr-5" color="success" @change="setColorType"></v-switch>
							<v-switch v-model="exterior" label="Экстерьер" color="success" @change="setColorType"></v-switch>
						</v-toolbar-items>
					</template>
				</v-toolbar>
			</v-col>
		</v-row>
		
		<v-row v-show="showElements || label === 'Цвет'">
			<v-col>
				<v-data-table
					:headers="headers"
					:items="label !== 'Цвет' ? proxyValues : filteredColorsItems"
					:items-per-page="10"
					:loading="false"
					class="elevation-1"
				>
					<template v-if="label !== 'Цвет'" v-slot:header.close="{ item }">
						<v-btn
							icon
							@click="showElements = false"
						>
							<v-icon>mdi-close</v-icon>
						</v-btn>
					</template>
					<template v-if="label === 'Цвет'" v-slot:item.name="{ item }">
						<div class="d-flex">
							<v-sheet width="22" height="22" :style="{'background-color': item.value }" class="mr-2"></v-sheet>{{ item.name }}
						</div>
					</template>
					//редактируемое поле
					<template v-if="label === 'Модификация'" v-slot:item.value="{ item }">
						<template v-if="!item.edited">
							{{ item.value }}
						</template>
						<v-text-field
							v-else
							v-model="item.value"
							single-line
							dense
							:loading="item.loading"
							:append-icon="'save'"
							@click:append="saveValue(item.id)"
						></v-text-field>
					</template>
					<template v-slot:item.action="{ item }">
						<v-tooltip top  v-if="label === 'Модификация'">
							<template v-slot:activator="{ on }">
								<v-icon
									class="mr-2"
									@click.stop="makeEditable(item.id)"
									v-on="on"
								>
									edit
								</v-icon>
							</template>
							<span>Редактировать</span>
						</v-tooltip>
						<v-tooltip top>
							<template v-slot:activator="{ on }">
								<v-icon
									@click.stop="snackbar = true"
									v-on="on"
								>
									delete
								</v-icon>
							</template>
							<span>Удалить</span>
						</v-tooltip>
					</template>
				</v-data-table>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
import CarPageTabs from '../components/CarPageTabs'
import CarPageLine from '../components/CarPageLine'

export default {
	name: 'CarPageTable',
	components: { CarPageTabs, CarPageLine },
	props: {
		label: {
			type: String,
			required: true
		},
		title: {
			type: String,
			required: true
		},
		buttonText: {
			type: String,
			required: true
		},
		headers: {
			type: Array,
			required: true
		},
		values: {
			type: Array,
			required: true
		},
		items: {
			type: Array,
			required: true
		},
		currentItem: {
			type: Number,
			required: true
		}
	},
	data () {
		return {
			showElements: false,
			interior: true,
			exterior: true,
			proxyValues: [],
			currentColorType: []
		}
	},
	computed: {
		filteredColorsItems () {
			let arr = [...this.proxyValues];

			return arr.filter((item)=>this.currentColorType.indexOf(item.type) >= 0)
		}
	},
	methods: {
		setColorType () {
			this.currentColorType = [this.exterior ? 'экстерьер' : false, this.interior ? 'интерьер' : false]
		},
		makeEditable (index) {
			this.proxyValues = this.proxyValues.map(item=>{

				if (item.id === index)	item.edited = true

				return item
			})
		},
		saveValue (index) {
			this.proxyValues = this.proxyValues.map(item=>{

				if (item.id === index) {
					item.loading = true
				}

				return item
			})
			setTimeout(() => {
					this.proxyValues = this.proxyValues.map(item=>{

						if (item.id === index) {
							item.loading = false
							item.edited = false
							this.$root.$emit('alert', {text: 'Значение сохранено', type: 'success'})
						}

						return item
					})
			}, 500);
		},
		openAttachColor () {
			this.$root.$emit('open-attach-color-popup')
		}
	},
	mounted () {
		this.setColorType();

		this.proxyValues = [...this.values];
	}

}
</script>

