<template>
	<v-card flat>
		<v-toolbar flat color="white" height="100">
			<v-toolbar-title class="font-weight-bold">{{ title }}</v-toolbar-title>
			<v-btn color="success" dark class="ml-auto" @click="openPopup">{{ buttonText }}</v-btn>
		</v-toolbar>
		<v-tabs vertical @change="setTab" :value="activeTab">
			<v-tab v-for="item in items" :key="item.id">
				{{ item.name }}
			</v-tab>

			<v-tab-item v-for="(item, index) in items" :key="index">
				<v-card flat>
					<v-card-text>
						<car-page-line :label="label" :show-drop-down="false" :items="items"  :current-item="currentItem" :style="{ 'margin-top': (index * 48) + 'px' }"></car-page-line>
					</v-card-text>
				</v-card>
			</v-tab-item>
		</v-tabs>
		<div class="pa-5 d-flex justify-end">
			<v-btn color="primary" dark class="ml-auto" @click.prevent="showElements">Показать элементы</v-btn>
		</div>
	</v-card>
</template>

<script>
import CarPageLine from '../components/CarPageLine'

export default {
	name: 'CarPageTabs',
	components: { CarPageLine },
	props: {
		label: {
			type: String,
			required: true
		},
		title: {
			type: String,
			required: true
		},
		buttonText: {
			type: String,
			required: true
		},
		items: {
			type: Array,
			required: true
		},
		currentItem: {
			type: Number,
			required: true
		}
	},
	data () {
		return {
			selectedTab: 0
		}
	},
	computed: {
		proxyCurrentItem: {
			get () {
				return this.currentItem
			},
			set (value) {
				this.$root.$emit('set-current', {value, label: this.label})
			}
		},
		activeTab () {
			let tab;

			this.items.forEach((item, i) => {
				if (item.id == this.proxyCurrentItem) tab = i
			});

			return tab
		}
	},
	methods: {
		setTab (index) {
			this.proxyCurrentItem = this.items[index].id
		},
		showElements () {
			this.$emit('show-elements', true);
		},
		openPopup () {
			if (this.label === 'Модификация') {
				this.$root.$emit('open-create-modification-popup', 0)
			}
			if (this.label === 'Комплектация') {
				this.$root.$emit('open-create-complectation-popup', 0)
			}
		}
	}
}
</script>

