<template>
	<div>
		<div class="d-flex align-center justify-space-between">
			<div v-if="showDropDown" class="mr-8 flex-grow-0" style="max-width: 15%;">
				<v-select
					v-model="proxyCurrentItem"
					:items="items"
					item-text="name"
					item-value="id"
					:label="label"
				></v-select>
			</div>
			<v-flex class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">ID:</strong><span v-on="on">{{ currentItemData.id }}</span>
					</template>
					<span>{{ currentItemData.id }}</span>
				</v-tooltip>
			</v-flex>
			<v-flex class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">Name:</strong><span v-on="on">{{ currentItemData.name }}</span>
					</template>
					<span>{{ currentItemData.name }}</span>
				</v-tooltip>	
			</v-flex>
			<v-flex v-if="label === 'Комплектация'" class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">Car Type:</strong><span v-on="on">{{ currentItemData.carType }}</span>
					</template>
					<span>{{ currentItemData.carType }}</span>
				</v-tooltip>
			</v-flex>
			<v-flex v-if="label === 'Комплектация'" class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">Car modification:</strong><span v-on="on">{{ currentItemData.carModification }}</span>
					</template>
					<span>{{ currentItemData.carModification }}</span>
				</v-tooltip>
			</v-flex>
			<v-flex class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">Short Description:</strong><span v-on="on">{{ currentItemData.shortDescription }}</span>
					</template>
					<span>{{ currentItemData.shortDescription }}</span>
				</v-tooltip>
			</v-flex>
			<v-flex v-if="label === 'Модификация'" class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">Car Type ID:</strong><span v-on="on">{{ currentItemData.carTypeId }}</span>
					</template>
					<span>{{ currentItemData.carTypeId }}</span>
				</v-tooltip>
			</v-flex>
			<v-flex v-if="label === 'Комплектация'" class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">Цена:</strong><span v-on="on">{{ currentItemData.price | priceFormat }}</span>
					</template>
					<span>{{ currentItemData.price | priceFormat }}</span>
				</v-tooltip>
			</v-flex>
			<v-flex v-if="label === 'Модификация'" class="overflow-value">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<strong class="mr-2">Порядок:</strong><span v-on="on">{{ typeof currentItemData.order === 'undefined' ? '(not set)' : currentItemData.order }}</span>
					</template>
					<span>{{ typeof currentItemData.order === 'undefined' ? '(not set)' : currentItemData.order }}</span>
				</v-tooltip>
			</v-flex>
			<div class="mr-5">
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<v-icon
							small
							class="mr-2"
							@click.stop="edit"
							v-on="on"
						>
							edit
						</v-icon>
					</template>
					<span>Редактировать</span>
				</v-tooltip>
				<v-tooltip top>
					<template v-slot:activator="{ on }">
						<v-icon
							small
							@click.stop="snackbar = true"
							v-on="on"
						>
							delete
						</v-icon>
					</template>
					<span>Удалить</span>
				</v-tooltip>
			</div>
		</div>
		<div v-if="label === 'Комплектация'" class="d-flex align-center">
			<v-tooltip top>
				<template v-slot:activator="{ on }">
					<strong class="mr-2">Пакеты:</strong><span v-on="on">{{ currentItemData.packets ? currentItemData.packets.reduce((prev, current)=>prev + ', ' + current) : '' }}</span>
				</template>
				<span>{{ currentItemData.packets ? currentItemData.packets.reduce((prev, current)=>prev + ', ' + current) : '' }}</span>
			</v-tooltip>
		</div>
	</div>
</template>

<script>
export default {
	name: 'CarPageLine',
	props: {
		label: {
			type: String,
			required: true
		},
		showDropDown: {
			type: Boolean,
			required: true
		},
		items: {
			type: Array,
			required: true
		},
		currentItem: {
			type: Number,
			required: true
		}
	},
	data () {
		return {
			
		}
	},
	computed: {
		proxyCurrentItem: {
			get () {
				return this.currentItem
			},
			set (value) {
				this.$root.$emit('set-current', {value, label: this.label})
			}
		},
		currentItemData () {
			let obj = {};

			this.items.forEach(item => {
				if (item.id == this.proxyCurrentItem) Object.assign(obj, item)
			});

			return obj
		}
	},
	methods: {
		edit () {
			if (this.label === 'Модификация') {
				this.$root.$emit('open-create-modification-popup', this.proxyCurrentItem)
			}
			if (this.label === 'Комплектация') {
				this.$root.$emit('open-create-complectation-popup', this.proxyCurrentItem)
			}
		}
	}
}
</script>

<style lang="sass">
.overflow-value
	overflow: hidden
	text-overflow: ellipsis
	white-space: nowrap
	margin-right: 5px
</style>