<template>
	<div>
		<div class="white mb-5">
			<v-container>
				<v-row>
					<v-col cols="12" md="4" class="d-flex flex-column justify-center align-center">
						<div>
							<h3>Car type</h3>
							<h1>Car Name</h1>
						</div>
					</v-col>
					<v-col cols="12" md="8">
						<v-img :src="require('@/assets/img/RS6.jpg')">
							<template v-slot:placeholder>
								<v-row
									class="fill-height ma-0"
									align="center"
									justify="center"
								>
									<v-progress-circular indeterminate color="success"></v-progress-circular>
								</v-row>
							</template>
						</v-img>
					</v-col>
				</v-row>
			</v-container>
		</div>
		<v-container class="pt-0 pb-0">
			<v-row>
				<v-col>
					<v-tabs class="ma-auto" height="64" grow>
						<v-tab>МОДИФИКАЦИЯ</v-tab>
						<v-tab>КОМПЛЕКТАЦИЯ</v-tab>
						<v-tab>ЦВЕТА</v-tab>
						<v-tab>ФОТОГРАФИИ</v-tab>

						<v-tab-item>
							<v-card flat>
								<car-page-table label="Модификация" title="Выберите модификацию" button-text="Создать модификацию" :current-item="currentModification" :headers="modificationHeaders" :values="modificationItems" :items="modifications"></car-page-table>
							</v-card>
						</v-tab-item>
						<v-tab-item>
							<v-card flat>
								<car-page-table label="Комплектация" title="Выберите комплектацию" button-text="Создать комплектацию" :current-item="currentComplectation" :headers="complectationHeaders" :values="complectationItems" :items="complectations"></car-page-table>
							</v-card>
						</v-tab-item>
						<v-tab-item>
							<v-card flat>
								<car-page-table label="Цвет" title="Цвета" button-text="Привязать цвет" :current-item="0" :headers="colorHeaders" :values="colorItems" :items="[]"></car-page-table>
							</v-card>
						</v-tab-item>
						<v-tab-item>
							<v-card flat>
								360
							</v-card>
						</v-tab-item>
					</v-tabs>
				</v-col>
			</v-row>
		</v-container>

		<car-page-create-modification-popup :is-opened="dialogM" @close-dialog="dialogM = false" :id="modificationId"></car-page-create-modification-popup>
		<car-page-create-complectation-popup :is-opened="dialogC" @close-dialog="dialogC = false" :id="complectationId"></car-page-create-complectation-popup>
		<car-page-attach-color-popup :is-opened="dialogColor" @close-dialog="dialogColor = false"></car-page-attach-color-popup>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import CarPageTable from '../components/CarPageTable'
import CarPageCreateComplectationPopup from '../components/CarPageCreateComplectationPopup'
import CarPageCreateModificationPopup from '../components/CarPageCreateModificationPopup'
import CarPageAttachColorPopup from '../components/CarPageAttachColorPopup'

export default {
	name: 'CarPage',
	components: { CarPageTable, CarPageCreateComplectationPopup, CarPageCreateModificationPopup, CarPageAttachColorPopup },
	data () {
		return {
			modificationHeaders: [
				{
					text: '#',
					align: 'left',
					sortable: false,
					value: 'num',
				},
				{
					text: 'ID',
					align: 'left',
					sortable: true,
					value: 'id',
				},
				{
					text: 'Type',
					align: 'left',
					sortable: true,
					value: 'type',
				},
				{
					text: 'Name',
					align: 'left',
					sortable: true,
					value: 'name',
				},
				{
					text: 'Units',
					align: 'left',
					sortable: true,
					value: 'units',
				},
				{
					text: 'Value',
					align: 'left',
					sortable: false,
					value: 'value',
				},
				{
					text: '',
					align: 'left',
					sortable: false,
					value: 'action',
				},
				{
					text: 'close',
					align: 'right',
					sortable: false,
					value: 'close',
				},
			],
			complectationHeaders: [
				{
					text: '#',
					align: 'left',
					sortable: false,
					value: 'num',
				},
				{
					text: 'ID',
					align: 'left',
					sortable: true,
					value: 'id',
				},
				{
					text: 'Type',
					align: 'left',
					sortable: true,
					value: 'type',
				},
				{
					text: 'Name',
					align: 'left',
					sortable: true,
					value: 'name',
				},
				{
					text: '',
					align: 'left',
					sortable: false,
					value: 'action',
				},
				{
					text: 'close',
					align: 'right',
					sortable: false,
					value: 'close',
				},
			],
			colorHeaders: [
				{
					text: '#',
					align: 'left',
					sortable: false,
					value: 'num',
				},
				{
					text: 'ID',
					align: 'left',
					sortable: true,
					value: 'id',
				},
				{
					text: 'Комплектация',
					align: 'left',
					sortable: true,
					value: 'complectation',
				},
				{
					text: 'Категория цвета',
					align: 'left',
					sortable: true,
					value: 'category',
				},
				{
					text: 'Тип цвета',
					align: 'left',
					sortable: true,
					value: 'type',
				},
				{
					text: 'Name',
					align: 'left',
					sortable: true,
					value: 'name',
				},
				{
					text: '',
					align: 'left',
					sortable: false,
					value: 'action',
				}
			],
			modificationItems: [
				{
					num: 1,
					id: 199,
					type: 'Двигатель',
					name: 'Автоматическая КП'
				},
				{
					num: 2,
					id: 299,
					type: 'Двигатель',
					name: 'Объём двигателя',
					units: 'л.с.',
					value: 1001
				}
			],
			complectationItems: [
				{
					num: 1,
					id: 199,
					type: 'Двигатель',
					name: 'Автоматическая КП'
				},
				{
					num: 1,
					id: 299,
					type: 'Двигатель',
					name: 'Объём двигателя'
				}
			],
			colorItems: [
				{
					num: 1,
					id: 199,
					complectation: 'Active',
					category: 'solid',
					type: 'экстерьер',
					name: 'Red',
					value: '#ff0000'
				},
				{
					num: 2,
					id: 299,
					complectation: 'Active',
					category: 'solid',
					type: 'экстерьер',
					name: 'Green',
					value: '#00ff00'
				},
				{
					num: 3,
					id: 399,
					complectation: 'Active',
					category: 'solid',
					type: 'интерьер',
					name: 'Кожа',
					value: '#0000ff'
				}
			],
			dialogC: false,
			dialogM: false,
			dialogColor: false,
			modificationId: 0,
			complectationId: 0
		}
	},
	computed: {
		...mapGetters({
			modifications: 'GET_MODIFICATIONS',
			complectations: 'GET_COMPLECTATIONS',
			currentModification: 'GET_CURRENT_MODIFICATION',
			currentComplectation: 'GET_CURRENT_COMPLECTATION'
		})
	},
	methods: {
		setCurrentItem (value) {
			if (value.label === 'Модификация') {
				this.$store.dispatch('SET_CURRENT_MODIFICATION', value.value);
			}
			if (value.label === 'Комплектация') {
				this.$store.dispatch('SET_CURRENT_COMPLECTATION', value.value);
			}
		},
		openCreateModificationPopup (id) {
			this.modificationId = id
			this.dialogM = true
		},
		openCreateComplectationPopup (id) {
			this.complectationId = id
			this.dialogC = true
		},
		openAttachColorPopup () {
			this.dialogColor = true
		}
	},
	mounted () {
		//Модификация по умолчанию
		this.$store.dispatch('SET_CURRENT_MODIFICATION', this.modifications[0].id);
		//Комплектация по умолчанию
		this.$store.dispatch('SET_CURRENT_COMPLECTATION', this.complectations[0].id);

		this.$root.$on('set-current', this.setCurrentItem)
		this.$root.$on('open-create-complectation-popup', this.openCreateComplectationPopup)
		this.$root.$on('open-create-modification-popup', this.openCreateModificationPopup)
		this.$root.$on('open-attach-color-popup', this.openAttachColorPopup)
	}
}
</script>

