<template>
	<v-dialog v-model="proxyIsOpened" max-width="1216px">
		<v-card class="pa-10 pb-0">
			<v-flex class="d-flex justify-end">
				<v-btn
					icon
					@click="closeThis"
				>
					<v-icon>mdi-close</v-icon>
				</v-btn>
			</v-flex>
			<v-card-text class="pa-0">
				<v-container>
					<v-row>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.name" label="Name"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.shortDescription" label="Short Description"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.carTypeId" label="Car Type ID"></v-text-field>
						</v-col>
						<v-col cols="12" sm="6" md="4">
							<v-text-field outlined v-model="editedItem.order" label="Порядок"></v-text-field>
						</v-col>
					</v-row>
				</v-container>
			</v-card-text>

			<v-card-actions class="pa-0">
				<v-container>
					<v-row>
						<v-col>
							<v-divider></v-divider>
						</v-col>
					</v-row>
					<v-row>
						<v-col>
							<v-btn color="success" dark @click="closeThis">Сохранить</v-btn>
						</v-col>
					</v-row>
				</v-container>
			</v-card-actions>
		</v-card>
	</v-dialog>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
	name: 'CarPageCreateModificationPopup',
	props: {
		isOpened: {
			type: Boolean,
			requared: true
		},
		id: {
			type: Number,
			requared: true
		}
	},
	data () {
		return {
			editedItem: {
				name: '',
				shortDescription: '',
				carTypeId: '',
				order: 0,
				price: 0
			}
		}
	},
	computed: {
		...mapGetters({
			modifications: 'GET_MODIFICATIONS',
		}),
		proxyIsOpened: {
			get () {
				return this.isOpened
			},
			set () {
				this.$emit('close-dialog')
			}
		}
	},
	methods: {
		closeThis () {
			this.$emit('close-dialog')
		}
	},
	watch: {
		id () {
			if (this.id !== 0) {
				this.modifications.forEach(mod => {
					if (mod.id === this.id) {
						Object.assign(this.editedItem, mod)
					}
				});
			} else {
				Object.assign(this.editedItem,  {
					name: '',
					shortDescription: '',
					carTypeId: '',
					order: 0,
					price: 0
				})
			}
		}
	}
}
</script>

